.nav {
  padding-top: 3rem;
  height: 5rem;
  color: whitesmoke;
  background: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-icon {
  padding-left: 4rem;
  transform: scale(0.8);
}

.nav_line {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.2);
  height: 1px;
  width: 40vw;
  margin-left: 10vw;
  z-index: 1;
}

.nav-menu {
  background-color: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(1rem);
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 52vw;
  height: 5rem;
  padding-left: 4rem;
  padding-right: 4rem;
  transition: all 500ms;
}

.nav-menu div {
  display: flex;
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 400;
  font-size: 1.2rem;
  letter-spacing: 0.2rem;
  align-items: center;
  height: 5rem;
  cursor: pointer;
}

.nav-menu div:active {
  /* border-bottom: 2px solid white;
  border-top: 2px solid transparent; */
  color: grey;
}

/* .nav-menu div:hover {
  border-bottom: 2px solid grey;
  border-top: 2px solid transparent;
} */

.nav-menu div span {
  font-weight: 700;
}

.nav-menu a:focus {
  color: grey;
}

a {
  margin-top: 1.4rem;
  height: 3.2rem;
}

.nav .active {
  border-bottom: 2px solid grey;
  border-top: 2px solid transparent;
}
